a{
    text-decoration: none;
    color: white;
    text-shadow: 1px 1px 5px  black;
}
.home-page{
    margin: 15px;
 
    border-radius: 15px;
    height: 100%;
    position: relative;
}

.roomsList{
    margin: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}
.roomCube{
    width: 150px;
    height: 150px;
    margin: 10px;
    border: 1px solid tan;
    border-radius: 15px;
}
.miniAddRoom{
    position: absolute;
    right: 50px;
    bottom: 10px;
    width: 100px;
    height: 100px;
    border-radius: 100px;
    border: none;
    background-color: rgb(123, 123, 123);
    color: white;
    font-size: 75px;
}
.Add-Room-btn{
    right: 50px;
    bottom: 10px;
    width: 100px;
    height: 100px;
    border-radius: 100px;
    border: none;
    background-color: rgb(123, 123, 123);
    color: white;
    font-size: 75px;
}
.addRoom-page{
    position: relative;
}
.addRoom-container{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-width: 500px;
    min-height: 100px;
    margin: auto;
}
.inputs{
    height: 40px;
    margin: 15px;
    border-radius: 10px;
    border: 1px solid black;
    font-size: 25px;
    text-align: center;
}
.selectors{
    height: 40px;
    font-size: 20px;

    margin: 15px;
}

.crate-btn{
    height: 50px;
    width: 150px;
    background-image: linear-gradient(to right, rgb(0, 162, 255) , rgb(0, 255, 200));
    border: none;
    border-radius: 15px;
    font-size: 20px;
    color: white;
    font-weight: bold;
}
.back-btn{
    border: none;
    border-radius: 15px;
    font-size: 20px;
    color: white;
    font-weight: bold;
    background-color: transparent;
    font-size: 40px;
}